#resume-items-list {
    top: 100px;
}

#resume-items-list ul {
    padding: 0px;
}

.item-container {
    display: flex;
    flex-direction: row;
    align-items: center;
}

#dash {
    width: 20px;
    border-bottom: 2px solid $secondary;
}

.list-group a {
    color: $title;
    background-color: $primary;
    left: 0px;
    border: 0px;
    border-radius: 0px;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    transition: left 0.3s;
}

.list-group a:hover {
    //left: 20px;
    //transition: left 0.3s;
    color: $secondary;
    background-color: $primary;
}

.list-group a.active {
    color: $secondary;
    background-color: transparent;
    left: 20px;
}

a.list-group-item.list-group-item-action.active::after {
    width: 10px;
    opacity: 1;
}

a.list-group-item.list-group-item-action::after {
    position: absolute;
    left: 0;
    bottom: 50%;
    width: 0%;
    height: 2px;
    background: $secondary;
    content: "";
    opacity: 0;
    transition: all 0.3s;
}

a.list-group-item.list-group-item-action:hover::after {
    width: 10px;
    opacity: 1;
}

.resume-subTitle {
    color: $secondary;

    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
}

.resume-subHeading {
    color: $title;

    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
}

.resume-heading {
    color: $heading;

    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
}

.list-item-container {
    display: flex;
    flex-direction: row;
    width: inherit;
    height: fit-content;
    margin-bottom: 40px;
    border-bottom: 2px solid $primary-light;
}

.list-item-container .info {
    padding-left: 20px;
    padding-bottom: 40px;
}

.resume-desc {
    color: $title;

    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    text-align: justify;
}

.resume-circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    background-color: $secondary;
    border-radius: 1000px;
}

.resume-icon {
    font-size: 26px;
    color: $primary;
    padding: 0px;
}

.list-container {
    height: fit-content;
    margin-bottom: 40px;
}

.card-container {
    display: flex;
    flex-direction: column;
    background-color: $primary-light;
    width: 230px;
    height: 338px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.card-heading {
    position: absolute;
    width: inherit;
    text-align: center;
    padding: 20px;
}

.card-skill-title {
    color: $secondary;

    font-style: normal;
    font-size: 20px;
    line-height: 23px;
}

.card-progress-container {
    width: inherit;
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
}

.percentage-circle {
    position: absolute;
}

.card-progress-container svg {
    transform: rotate(-90deg);
}

.card-progress-container svg circle {
    // width: 230px;
    // height: 230px;
    stroke-dasharray: 440;
    stroke-dashoffset: 440;
}

.card-progress-container svg circle:nth-child(1) {
    fill: transparent;
    stroke: $secondary;
    stroke-opacity: 0.3;
    stroke-width: 3px;
    stroke-dashoffset: 0;
}

.card-progress-container svg circle:nth-child(2) {
    fill: transparent;
    stroke: $secondary;
    stroke-width: 3px;
    stroke-linecap: round;
    //stroke-dashoffset: 440 - (440 * 70) / 100;
}

.skill-card {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 50px;
}

.skill-box-container {
    background-color: $primary-light;
}

.skill-box {
    padding: 20px;
    height: 450px;
}

.skills-but-container {
    display: block;
    width: inherit;
}

.skill-box-container-sm {
    display: none;
}

.skills-but-group {
    width: 100%;
}

#togal-but {
    color: $secondary;
    font-size: 20px;
    background-color: transparent;
    border: 0px;
    border-radius: 0px;
    outline: none;
    padding-left: 15px;
    padding-right: 15px;
    box-shadow: none;
    padding: 15px;
}

#togal-but.active,
#togal-but:hover {
    color: $primary;
    cursor: pointer;
    background-color: $secondary;
}

.skillName {
    color: $secondary;
}
.skillName.active {
    color: red;
}

.skill-info {
    margin-bottom: 20px;
}

.skill-bar {
    height: 5px;
    background-color: $secondary;
}

