@media only screen and (max-width: $mobileScreen) {
    .resume-icon {
        font-size: 18px;
        padding: 15px;
    }

    .skillName-title {
        color: $secondary;
        font-weight: bold;
        font-size: 24px;
    }

    .page-title.opacity-05 {
        opacity: 0.5;
        padding: 0px;
        font-size: 20px;
    }

    .resume-subTitle {
        font-size: 16px;
        margin-bottom: 10px;
    }

    .resume-heading {
        font-size: 18px;
        margin-bottom: 10px;
    }

    .resume-subHeading {
        font-size: 16px;
        margin-bottom: 10px;
    }

    .list-group-container {
        display: none;
    }

    .resume-desc {
        font-size: 14px;
        margin-bottom: 10px;
    }

    .list-item {
        padding: 0px;
    }

    .resume-items-list {
        display: none;
    }

    .skill-card,
    .card-container {
        display: none;
    }

    .skill-box-container {
        background-color: $primary;
    }

    .skills-but-container {
        display: none;
    }

    .skill-box {
        padding: 20px 0px;
    }

    .skill-box-container-sm {
        display: flex;
        width: 100%;
    }

    .active-skill {
        flex: 1;
        display: flex;
        //justify-content: center;
        align-items: center;
        font-size: 24px;
    }

    .right-but a,
    .left-but a {
        cursor: pointer;
    }
}
